@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: white;
  /* border: black 1px solid; */
}

h1,
h2,
h3,
a,
button {
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
}

#page {
  /* border: #f13535 solid; */
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
}

#container {
  display: flex;
  position: relative;
  /* border: solid black; */
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
}

#spacer1 {
  height: 50px;
  z-index: -10;
}

#spacer2 {
  height: 200px;
  z-index: -10;
}

#content {
  z-index: 1;
  background-color: antiquewhite;
  display: flex;
  position: relative;
  /* border: solid red; */
  height: 300px;
  width: 60%;
  padding: 25px;
  text-align: left;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#content:hover {
  /* box-shadow: 10px 10px black; */
  z-index: 1;
}

#nextButtonContainer {
  position: absolute;
  /* border: solid black; */
  height: 100px;
  width: 100px;
  display: flex;
  left: 88%;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

#backButtonContainer {
  position: absolute;

  height: 10px;
  width: 100px;
  display: flex;
  right: 88%;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

#nextButton {
  height: 100%;
  width: 100%;
  cursor: pointer;
  background-color: whitesmoke;
  border: none;
}

#nextButton:hover {
  box-shadow: 6px 6px black;
}

#nextArrow {
  height: 100%;
  width: 100%;
  mix-blend-mode: multiply;
}

#backArrow {
  transform: scaleX(-1);
  height: 100%;
  width: 100%;
  mix-blend-mode: multiply;
}

#imageContainer {
  display: flex;
  position: absolute;

  justify-content: center;
  align-items: center;
  width: 60%;
}

#screenshot1 {
  height: 400px;
  z-index: 3;
  mix-blend-mode: multiply;
}

#userInputContainer {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 300px;
  left: 0;
  flex-direction: column;
  background-color: antiquewhite;
}

#userOutputContainer {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 300px;
  right: 0;
  flex-direction: column;
  background-color: antiquewhite;
  padding: 25px;
}

#userInput {
  width: 60%;
  height: 50px;
  font-size: 16px;
  text-align: center;
}

#submitTextButton {
  width: 100px;
  height: 30px;
  border-radius: 8px;
  background-color: palevioletred;
  color: white;
  cursor: pointer;
  border: 3px black solid;
  box-shadow: 3px 3px black;
}

#submitTextButton:hover {
  background-color: rgba(216, 112, 147, 0.75);
}

#userOutput {
  display: flex;
  font-size: 38px;
  width: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 300px;
  box-shadow: 6px 6px black;
  border: black 3px solid;
}

#userDangerousOutput {
  display: flex;
  font-size: 38px;
  width: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 300px;
  box-shadow: 6px 6px black;
  border: black 3px solid;
}

#userLinkOutput {
  display: flex;
  font-size: 18px;
  width: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 300px;
  box-shadow: 6px 6px black;
  border: black 3px solid;
}
